<template>
  <v-container class="my-10">
    <v-card class="pa-4 pa-md-16 elevation-5" rounded="xl">
      <h1 class="mb-8">Fidelización persona</h1>
      <v-alert
        transition="scale-transition"
        v-show="alertSuccess"
        type="success"
      >
        Información enviada con exito
      </v-alert>
      <v-alert transition="scale-transition" v-show="alertError" type="error">
        Ocurrió un error
      </v-alert>
      <v-alert transition="scale-transition" v-show="alertWarning" type="warning">
        El usuario ya existe
      </v-alert>
      <validation-observer ref="observer" v-slot="{ invalid, reset }">
        <v-form ref="form" @submit.prevent="onSubmit" @reset.prevent="reset">
          <v-row>
            <v-col>
              <validation-provider
                name="Nombre Completo"
                rules="required"
                v-slot="{ errors }"
              >
                <v-text-field
                  color="yellow darken-1"
                  :error-messages="errors"
                  v-model="nuevaPersona.name"
                  label="Nombre Completo"
                  outlined
                  required
                >
                </v-text-field>
              </validation-provider>
            </v-col>
            <v-col>
              <validation-provider
                name="Documento de identidad"
                rules="required"
                v-slot="{ errors }"
              >
                <v-text-field
                type="number"
                  color="yellow darken-1"
                  :error-messages="errors"
                  v-model="nuevaPersona.cc"
                  prepend-inner-icon="mdi-pound"
                  label="C.C"
                  outlined
                  required
                >
                </v-text-field>
              </validation-provider>
            </v-col>
          </v-row>

          <v-row>
            <v-col>
              <v-text-field
              type="number"
                color="yellow darken-1"
                label="Celular"
                v-model="nuevaPersona.phoneNumber"
                prepend-inner-icon="mdi-pound"
                placeholder="+57"
                outlined
              >
              </v-text-field>
            </v-col>
            <v-col>
              <validation-provider
                v-slot="{ errors }"
                name="Correo Electrónico"
                rules="email"
              >
                <v-text-field
                  color="yellow darken-1"
                  :error-messages="errors"
                  label="Correo Electrónico"
                  v-model="nuevaPersona.email"
                  placeholder="nombre@email.com"
                  outlined
                >
                </v-text-field>
              </validation-provider>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <validation-provider
                v-slot="{ errors }"
                name="Municipio"
                rules="required"
              >
                <v-select
                  color="yellow darken-1"
                  :items="dataCities"
                  :error-messages="errors"
                  prepend-inner-icon="mdi-map-marker"
                  label="Municipio"
                  v-model="nuevaPersona.city"
                  outlined
                ></v-select>
              </validation-provider>
            </v-col>
            <v-col>
              <validation-provider
                v-slot="{ errors }"
                name="Barrio"
                rules="required"
                v-if="nuevaPersona.city == 'Maicao'"
              >
                <v-select
                  color="yellow darken-1"
                  :items="dataBarrios"
                  :error-messages="errors"
                  label="Barrio"
                  v-model="nuevaPersona.barrio"
                  outlined
                  required
                >
                </v-select>
              </validation-provider>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6" md="2">
              <v-text-field
              type="number"
                color="yellow darken-1"
                label="Carrera"
                v-model="address.carrera"
                outlined
              >
              </v-text-field>
            </v-col>
            <v-col cols="6" md="2">
              <v-text-field
              type="number"
                color="yellow darken-1"
                label="Calle"
                v-model="address.calle"
                outlined
              >
              </v-text-field>
            </v-col>
            <v-col cols="5" md="2" class="ml-auto ml-md-0">
              <v-text-field
              type="number"
                color="yellow darken-1"
                prepend-icon="mdi-pound"
                label="Número"
                v-model="address.numero1"
                outlined
              >
              </v-text-field>
            </v-col>
            <v-col cols="5" md="2">
              <v-text-field
              type="number"
                color="yellow darken-1"
                class="ml-n4"
                prepend-icon="mdi-minus-thick"
                label="Número"
                v-model="address.numero2"
                outlined
              >
              </v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6" md="4">
              <v-text-field
                color="yellow darken-1"
                v-model="nuevaPersona.puesto"
                label="Puesto de votación"
                outlined
              ></v-text-field>
            </v-col>
            <v-col cols="6" md="1">
              <v-text-field
              type="number"
                color="yellow darken-1"
                v-model="nuevaPersona.mesa"
                label="Mesa"
                outlined
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                color="yellow darken-1"
                v-model="nuevaPersona.lider"
                label="Lider"
                outlined
              >
              </v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-btn
              :loading="loader"
              :disabled="invalid"
              class="gradient elevation-0 white--text mb-8 mb-md-auto"
              @click="sendData"
              block
            >
              Enviar
            </v-btn>
          </v-row>
        </v-form>
      </validation-observer>
    </v-card>
  </v-container>
</template>

<script>
import decode from "jwt-decode";
import { required, email } from "vee-validate/dist/rules";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from "vee-validate";

setInteractionMode("eager");

extend("required", {
  ...required,
  message: "El campo {_field_} no puede estar vacío",
});

extend("email", {
  ...email,
  message: "Debe introducir un correo valido",
});
export default {
  components: {
    ValidationProvider,
    ValidationObserver,
  },

  data: () => ({
    address: {
      carrera: "",
      calle: "",
      numero1: "",
      numero2: "",
    },

    alertSuccess: false,
    alertError: false,
    alertWarning:false,

    loader: false,

    dataCities: [],
    dataBarrios: [],

    nuevaPersona: {
      name: "",
      cc: null,
      phoneNumber: null,
      email: "",
      city: "",
      barrio: "",
      address: "",
      puesto: null,
      mesa: null,
      lider: "",
      user: "",
    },
  }),

  methods: {
    addressFinished() {

      this.nuevaPersona.address = "".concat(
        this.carreraFinished(this.address.carrera),
        this.calleFinished(this.address.calle),
        this.numero1Finished(this.address.numero1),
        this.numero2Finished(this.address.numero2)
      );
    },
    calleFinished(calle) {
      if(calle) {
        return "  cll ".concat(calle)
      } else return ""
    },
    numero1Finished(numero1) {
      if(numero1) {
        return " # ".concat(numero1)
      } else return ""
    },
    numero2Finished(numero2) {
      if(numero2) {
        return " - ".concat(numero2)
      } else return ""
    },
    carreraFinished(carrera) {
      if(carrera) {
        return "Kra ".concat(carrera)
      } else return ""
    },

    onSubmit() {
      if (this.$refs.observer.validate()) {
        return;
      }
    },
    sendData() {
      this.loader = true;
      this.addressFinished();
      this.axios
        .post("/send", this.nuevaPersona)
        .then(() => {
          this.$refs.observer.reset();
          this.name = this.cc = this.phoneNumber = "";
          this.loader = false;
          this.alertSuccess = true;
          this.$nextTick(() => {
            this.$refs.form.reset();
          });
          setTimeout(() => {
            this.alertSuccess = false;
          }, 5.0 * 1000);
        }, (err) => {
          console.log(err);
          if (err == "Error: Request failed with status code 418") {
            this.loader = false;
            this.alertWarning = true;

            setTimeout(() => {
              this.alertWarning = false;
          }, 5.0 * 1000);

          } else {
          this.loader = false;
          this.alertError = true;

          setTimeout(() => {
            this.alertError = false;
          }, 5.0 * 1000);}
        })

    },

    getDataCities() {
      try {
        this.axios.get("/list").then((res) => {
          this.dataCities = res.data;
        });
      } catch (err) {
        console.log(err);
      }
    },
    getDataBarrios() {
      try {
        this.axios.get("/list/barrios").then((res) => {
          this.dataBarrios = res.data;
        });
      } catch (err) {
        console.log(err);
      }
    },
  },

  mounted() {
    this.getDataCities();
    this.getDataBarrios();
    let token = decode(localStorage.getItem("token"));
    this.nuevaPersona.user = token.name;
  },
};
</script>

<style>
.gradient {
  background-image: radial-gradient(
    circle at -7.92% 9.44%,
    #d7c35d 0,
    #deba54 8.33%,
    #e4b04d 16.67%,
    #e9a546 25%,
    #ed9841 33.33%,
    #f0893e 41.67%,
    #f2793c 50%,
    #f3673d 58.33%,
    #f35542 66.67%,
    #f34249 75%,
    #f22b52 83.33%,
    #f0065d 91.67%,
    #ec0069 100%
  );
}
</style>
